 * overlay a background color and NOT the overall color scheme of the arrows
 * or the color an element main gain by applying this class.
 */
.billboard--arrow,
.module--arrows-white {
  background-image: url(data:image/svg+xml;charset=utf8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%201440%201145%22%3E%3Cpath%20opacity%3D%22.1%22%20fill%3D%22%23FFF%22%20d%3D%22M1312.8%20519.2L875%201145H0V0h949.5z%22%2F%3E%3Cpath%20opacity%3D%22.07%22%20fill%3D%22%23FFF%22%20d%3D%22M770.8%20656.5L429%201145H0V0h311.5z%22%2F%3E%3C%2Fsvg%3E);
