/**
 * Component Level Theming Utility Classes
 * -----------------------------------------------------------------------------
 *
 * Scoping classes used to theme a component and its contents.
 */
.tint-themed {
  color: #000000;
  background-color: #00baff; }

.tint-white {
  color: #000000;
  background-color: #ffffff; }

.tint-black {
  color: #ffffff;
  background-color: #323030; }

.tint-blackish {
  color: #ffffff;
  background-color: #323030; }

.tint-lightgray {
  color: #000000;
  background-color: #eaeaea; }

.theme-blue .tint-themed {
  color: #ffffff;
  background-color: #213f7b; }

.theme-maroon .tint-themed {
  color: #ffffff;
  background-color: #a90533; }

.theme-brown .tint-themed {
  color: #000000;
  background-color: #9b7b56; }

/* Hide snippets that have not been component themed. ----------------------- */
/**
 * Note: As of 6/2/20, almost all snippets have not been reviewed for
 * compatibility with component themes. As a result, some snippets may be
 * "disabled" when they do not need to be. As needed, we will review the
 * compatibly of a desired snippet and make any required changes at that time.
 *
 * For now, we are going to hide all snippets within a tint and enable specific
 * snippets, one-by-one.
 *
 * All deprecated `ous-` (with dash) snippets are hidden. To enable this version
 * of a snippet, update snippet to `.ous` (without dash), then add to the
 * enabled snippets list.
 */
[class*="tint-themed"] .ous:not(.columns):not(.sidebar-main-content):not(.stats):not(.embedded-media-basic):not(.callout-grid):not(.program-grid):not(.p-courseleaf-course-teasers),
[class*="tint-themed"] [class*="ous-"]:not(.ous-contact) {
  position: relative;
  height: 6rem;
  border: 4px solid rgba(255, 255, 255, 0.5);
  margin: 0; }
  [class*="tint-themed"] .ous:not(.columns):not(.sidebar-main-content):not(.stats):not(.embedded-media-basic):not(.callout-grid):not(.program-grid):not(.p-courseleaf-course-teasers)::before,
  [class*="tint-themed"] [class*="ous-"]:not(.ous-contact)::before {
    content: 'Snippet incompatible with Component Theming';
    position: absolute;
    top: 50%;
    left: 50%;
    color: #ffffff;
    font-size: 1rem;
    text-align: center;
    transform: translate(-50%, -50%); }
  [class*="tint-themed"] .ous:not(.columns):not(.sidebar-main-content):not(.stats):not(.embedded-media-basic):not(.callout-grid):not(.program-grid):not(.p-courseleaf-course-teasers) *,
  [class*="tint-themed"] [class*="ous-"]:not(.ous-contact) * {
    visibility: hidden !important; }

[class*="tint-black"] .ous:not(.columns):not(.sidebar-main-content):not(.stats):not(.embedded-media-basic):not(.callout-grid):not(.program-grid):not(.p-courseleaf-course-teasers),
[class*="tint-black"] [class*="ous-"]:not(.ous-contact) {
  position: relative;
  height: 6rem;
  border: 4px solid rgba(255, 255, 255, 0.5);
  margin: 0; }
  [class*="tint-black"] .ous:not(.columns):not(.sidebar-main-content):not(.stats):not(.embedded-media-basic):not(.callout-grid):not(.program-grid):not(.p-courseleaf-course-teasers)::before,
  [class*="tint-black"] [class*="ous-"]:not(.ous-contact)::before {
    content: 'Snippet incompatible with Component Theming';
    position: absolute;
    top: 50%;
    left: 50%;
    color: #ffffff;
    font-size: 1rem;
    text-align: center;
    transform: translate(-50%, -50%); }
  [class*="tint-black"] .ous:not(.columns):not(.sidebar-main-content):not(.stats):not(.embedded-media-basic):not(.callout-grid):not(.program-grid):not(.p-courseleaf-course-teasers) *,
  [class*="tint-black"] [class*="ous-"]:not(.ous-contact) * {
    visibility: hidden !important; }

/**
 * Display Utility Classes
 * -----------------------------------------------------------------------------
 *
 * Instead of creating our own, consider importing Bootstrap's display helpers.
 *
 * @see https://getbootstrap.com/docs/4.4/utilities/display/
 */
/**
 * Viewport Width Containers (in limited width parent).
 *
 * NOTE: REQUIRES THE PARENT TO BE EXACTLY CENTERED IN THE BROWSER.
 *
 * @see  https://css-tricks.com/full-width-containers-limited-width-parents/
 * @see  https://codepen.io/maddesigns/pen/rOMgpQ/
 * @todo Once SCSS files are re-organized, consider moving to a "layout" file.
 *       Also consider applying breakpoint options as well.
 */
.to-viewport-width {
  position: relative !important;
  left: 50% !important;
  right: 50% !important;
  width: 100vw !important;
  margin-left: -50vw !important;
  margin-right: -50vw !important; }

.d-flex {
  display: flex !important; }

.flex-column {
  flex-direction: column !important; }

/**
