 * Stretched Link - .stretched-link
 * Alignment - .align-
 * Positioning - .inline-block
 * Text - .text-
 * Hide - .hide
 * Lists - .ul-inline, .list-
 * Horizontal rule - hr
 * Close button (X) - .cross
