/**
 * Sidebar
 * -----------------------------------------------------------------------------
 *
 * Appears below the sidebar navigation.
 */
.sidebar__panel {
  margin-top: 2rem;
  margin-bottom: 2rem; }
  @media (min-width: 64em) {
    .sidebar__panel .feature {
      background-color: transparent; } }
  @media (min-width: 64em) {
    .sidebar__panel .feature__img {
      float: none; } }
  @media (min-width: 64em) {
    .sidebar__panel .feature__img img {
      width: 100%; } }
  @media (min-width: 64em) {
    .sidebar__panel .feature__content {
      padding: 1rem 0 0; }
      .sidebar__panel .feature__content .button--link, .sidebar__panel .feature__content .button--inline, .sidebar__panel .feature__content .courses__reset, .sidebar__panel .feature__content .tuition-table-disclaimer {
        margin-top: 0.5rem; } }
  @media (min-width: 64em) {
    .sidebar__panel blockquote cite {
      margin-top: 2rem; } }
  @media (min-width: 64em) {
    .sidebar__panel blockquote .blockquote__cite__name {
      font-size: 1.5rem; } }

.sidebar__divider {
  border-top: 4px solid #00baff;
  padding-top: 1rem; }
  .theme-blue .sidebar__divider {
    border-color: #213f7b; }
  .theme-maroon .sidebar__divider {
    border-color: #a90533; }
  .theme-brown .sidebar__divider {
    border-color: #9b7b56; }
  .sidebar__divider .social__list {
    margin-top: 2rem; }

.sidebar__form {
  margin: 4rem 0; }

.skip a {
  position: absolute;
  left: -10000rem;
  top: -2rem;
  width: 0.1rem;
  height: 0.1rem;
  overflow: hidden;
  z-index: 100;
  color: #ffffff;
  outline: 0;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  transition: top 1s ease-out; }
  .skip a:focus {
    left: 0;
    top: 0;
    width: auto;
    height: auto;
    background: #000000;
    transition: top 0.1s ease-in; }

.skip-subnav a {
  position: absolute;
  left: -10000rem;
  top: -2rem;
  width: 0.1rem;
  height: 0.1rem;
  overflow: hidden;
  z-index: 10;
  color: #ffffff;
  outline: 0;
  padding: 0.5rem 1rem;
  transition: left 1s ease-out; }
  .skip-subnav a:focus {
    left: 0;
    width: auto;
    height: auto;
    background: #000000;
    transition: left 0.1s ease-in; }

/**
 * Featured Slider
 * -----------------------------------------------------------------------------
 *
 * Landing and College Templates.
 * - College Template: uses the snippet as a built-in feature of the template
 *   via MultiEdit.
 * - Landing Template: user can place slider like a regular snippet in the
 *   Additional Content region only.
 *
 * The slider has two configuration options:
 * - Color Scheme/Style: black, themed
 * - ImageAlignment:     left, right
 *
 * -----
 *
 * As we move helper classes out of snippets to ease the burden of updating them
 * across sites, we need to consider the impact of removing helper classes like
 * `.module--black`. `.module--black` does some nice things for us like making
 * it simple to change the look of `.button--link` elements because of its
 * addition to a container element.
 *
 * At first thought, extending `.module--black` might seem like a good idea. But
 * that generates A LOT of extraneous selectors that won't ever target anything
 * inside of `.featured-slider`. If you extend an element with `.module--black`
 * multiple times, we'll end up adding quite a bit of bloat.
 *
 * It's also not ideal to re-create styles that are applied by the simple
 * addition of `.module--black` on a container element. Which is what we are
 * doing (for now) for this snippet.
 *
 * This might mean creating mixins or placeholders that do what `.module--black`
 * is doing now. Further investigation is required.
 */
@media (min-width: 72em) {
  .featured-slider--media-left .dot {
    color: #00baff;
    border-color: #00baff; }
    .theme-blue .featured-slider--media-left .dot {
      color: #213f7b !important;
      border-color: #213f7b !important; }
    .theme-maroon .featured-slider--media-left .dot {
      color: #a90533 !important;
      border-color: #a90533 !important; }
    .theme-brown .featured-slider--media-left .dot {
      color: #9b7b56 !important;
      border-color: #9b7b56 !important; } }

/**
 * Flickity Previous and Next buttons use an inline SVG unrelated to SVGStore.
 * It is flying solo. It does not use a data URI or `<use>` element like we see
 * everywhere else.
 *
 * Color styles for these icons are modified via CSS class and the CSS property
 * `fill`.
 *
 * @todo Investigate if we need to normalize this. Either move icons towards
 * this method or this icon to the other methods; data URI or `<use>` element.
 */
.flickity-prev-next-button, .flickity-prev-next-button:hover {
  background-color: #00baff; }
  .theme-blue .flickity-prev-next-button, .theme-blue .flickity-prev-next-button:hover {
    background-color: #ffffff; }
  .theme-maroon .flickity-prev-next-button, .theme-maroon .flickity-prev-next-button:hover {
    background-color: #ffffff; }
  .theme-brown .flickity-prev-next-button, .theme-brown .flickity-prev-next-button:hover {
    background-color: #9b7b56; }

.tint-themed .flickity-prev-next-button,
.module--green .flickity-prev-next-button {
  background-color: #000000; }

.theme-blue .tint-themed .flickity-prev-next-button, .theme-blue
.module--themed .flickity-prev-next-button {
  background-color: #ffffff; }

.theme-maroon .tint-themed .flickity-prev-next-button, .theme-maroon
.module--themed .flickity-prev-next-button {
  background-color: #ffffff; }

.theme-brown .tint-themed .flickity-prev-next-button, .theme-brown
.module--themed .flickity-prev-next-button {
  background-color: #000000; }

.theme-blue .tint-themed .flickity-prev-next-button .arrow, .theme-blue
.module--themed .flickity-prev-next-button .arrow {
  /**
         * THEMIFY_CALIBRATE, dark themes use theme color for the fill but there
         * may be times where it should be black. Create new theme map key or
         * switch the default from theme color to black.
         */
  fill: #213f7b; }

.theme-maroon .tint-themed .flickity-prev-next-button .arrow, .theme-maroon
.module--themed .flickity-prev-next-button .arrow {
  /**
         * THEMIFY_CALIBRATE, dark themes use theme color for the fill but there
         * may be times where it should be black. Create new theme map key or
         * switch the default from theme color to black.
         */
  fill: #a90533; }

.theme-brown .tint-themed .flickity-prev-next-button .arrow, .theme-brown
.module--themed .flickity-prev-next-button .arrow {
  /**
         * THEMIFY_CALIBRATE, dark themes use theme color for the fill but there
         * may be times where it should be black. Create new theme map key or
         * switch the default from theme color to black.
         */
  fill: #ffffff; }

.theme-blue .tint-black .flickity-prev-next-button .arrow, .theme-blue
.module--black .flickity-prev-next-button .arrow {
  fill: #213f7b; }

.theme-maroon .tint-black .flickity-prev-next-button .arrow, .theme-maroon
.module--black .flickity-prev-next-button .arrow {
  fill: #a90533; }

.theme-brown .tint-black .flickity-prev-next-button .arrow, .theme-brown
.module--black .flickity-prev-next-button .arrow {
  fill: #000000; }

.featured-slider--media-left .featured-slider__container {
