    padding-top: 3rem;
    padding-right: 5%; } }

@media (min-width: 72em) {
  .featured-slider--media-left .featured-slider__slider {
    margin-bottom: 4rem; }
    .featured-slider--college.featured-slider--media-left .featured-slider__slider {
      margin-bottom: 6rem !important; } }
    @media (min-width: 72em) and (min-width: 48em) {
      .featured-slider--college.featured-slider--media-left .featured-slider__slider {
        margin-bottom: 7rem !important; } }
    @media (min-width: 72em) and (min-width: 64em) {
